import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  private baseUrl:string="http://localhost:9090/api/";

  constructor(private http:HttpClient) { }

logincheck(obj){
return(this.http.post(`${this.baseUrl}logincheck`,obj));
}
getAllEmployee(){
  return (this.http.get(`${this.baseUrl}getAllEmployee`));
 }
 getParticularRecord(id:number){
  return (this.http.get(`${this.baseUrl}getParticularEMP/${id}`));
 }
 getAllCountry(){
  return (this.http.get(`${this.baseUrl}getAllCountry`));
}
addEmployee(obj){
  return (this.http.post(`${this.baseUrl}addemployee`,obj,{responseType:"text"}));
}
updateEmployee(obj){
  return  (this.http.put(`${this.baseUrl}updateEmp`,obj,{responseType:"text"}));
}
deleteEmployee(id:number){
  return  (this.http.delete(`${this.baseUrl}deleteEmp/${id}`,{responseType:"text"}));
}
getEmpStatus(id){
return(this.http.get(`${this.baseUrl}getStatus/${id}`,{responseType:"text"}));
}
}
