import { Component, Input, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Employee } from '../Employee';
import { HttpService } from '../http.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';


@Component({
  selector: 'app-empdetails',
  templateUrl: './empdetails.component.html',
  styleUrls: ['./empdetails.component.css']
})
export class EmpdetailsComponent implements OnInit {
  id:number=0;
  empobj =<Employee>{};
  oncheked:boolean=false;
  modalRef: BsModalRef;
  config = {
    animated: true,
    
    ignoreBackdropClick: true,
    class: "alert alert-danger"
  }; 
  
  constructor( private route:ActivatedRoute,
    private service:HttpService,
    private modalservice:BsModalService, ){}

  ngOnInit() {
    this.GetDataRetriveFromURL();
    this.GetDataFromBackend(this.id);
  }
  GetDataRetriveFromURL(){
    this.route.paramMap
    .subscribe((param)=>{
      this.id=+param.get("id");
    });
  }
  GetDataFromBackend(id:number){
    this.service.getParticularRecord(id)
    .subscribe((response)=>{
      this.empobj=(<Employee>response);
      console.log(this.empobj);
    })
  }
  
  OnUpdate(popUp:TemplateRef<any>){
   
      this.modalRef=this.modalservice.show(popUp,this.config);
    
  } 

}
